.panel-download { display: flex; flex-direction: column; border: 0; color: #fff; text-align: center; min-height: 425px; transition: all .3s ease-in-out; }

.panel-download h2 { margin-top: 20px; margin-bottom: 30px; }

.panel-download .panel-content { margin-top: 60px; }

.panel-download.bg-pb-gray-500 .panel-content { margin-top: 40px; }

.panel-download .icon { height: 72px; }

.panel-download ul li { margin-bottom: 8px; }

.panel-download a { color: #fff; font-weight: 200; }

.panel-download a:hover { color: #fff; }

.panel-download:hover { box-shadow: 0 11px 20px -5px rgba(0, 0, 0, 0.5); transition: all .3s ease-in-out; }

.panel-download.illustrator { background-color: #da1f26; }

hr { width: 50%; }

hr.web { border-color: #c68cc3; }

hr.ios { border-color: #717171; }

.object { background: #a03f9b; border-radius: 50%; width: 50px; height: 50px; display: block; margin: 10px 0; }

.object.green { background-color: #72bf44; }

.sandbox-wrapper { margin: 0; border: solid 1px #eaedf8; background-color: #fff; }

.sandbox-wrapper .controls { background-color: #f8f8f8; padding-top: 8px; height: 49px; border-top: solid 1px #eaedf8; border-right: solid 1px #eaedf8; }

.sandbox-wrapper .controls .btn-group { margin-bottom: 0; }

.sandbox-wrapper .panel-group { height: 400px; overflow-y: scroll; }

.sandbox-wrapper .more-info { text-transform: inherit; }

.sandbox-wrapper .more-info.selected { background-color: #2e2e2e; text-decoration: none; color: #fff; }

.sandbox-wrapper .more-info:hover:not(.selected) { background-color: #4e4e4e; }

.stage-wrapper { padding-left: 0; padding-right: 0; }

.stage-wrapper pre { height: 49px; margin-bottom: 0; border-radius: 0; line-height: 28px; border: 0; background-color: #f8f8f8; border-top: solid 1px #eaedf8; }

.stage { padding: 20px; height: 400px; background-color: #fff; }

.panel-group { margin-bottom: 0; }

.panel-group .panel .panel-heading { padding-left: 15px; font-size: 14px; }

.panel-group .panel .panel-heading label { margin-bottom: 0; }

.colors p { margin-top: 10px; }

.sample-app .panel-heading { color: #fff; border: 0; }

.type-samples h1, .type-samples h2, .type-samples h3, .type-samples h4, .type-samples h5, .type-samples h6 { margin: 0; }

.type-samples tr > td:first-child { width: 40%; }

.type-samples tr > td:last-child { font-family: Menlo, Consolas, Monaco, "Courier New", monospace; }

.type-samples tr > td:last-child .swatch { display: inline-block; min-width: 20px; height: 20px; border: 1px solid #c0c0c0; padding: 2px 5px; font-size: 12px; line-height: 1.2; border-radius: 3px; }

.more-info { background-color: #9b9b9b; padding: 5px 15px; border-radius: 20px; font-family: Menlo, Consolas, Monaco, "Courier New", monospace; font-size: 11px; margin: 5px; display: inline-block; text-transform: lowercase; color: #fff; font-weight: normal; }

.more-info:hover { color: #fff; background-color: #2e2e2e; text-decoration: none; }

.more-info.plugin:before { font-family: 'Nucleo Mini'; content: '\ee37'; padding-right: 7px; }

.donut-varied-padded { margin-right: 25px; margin-left: 25px; }

.fpo-grad { padding: 10px; background-position: center center; background-size: fit; }

.fpo-grad p.help-text { color: #fff; }

.glyphicons li { font-size: .8em; margin-bottom: 20px; text-align: center; }

.text-center .pie, .text-center .donut { width: 100px; margin: 0 auto 20px; }

.clearRow { clear: left; }

.pb-icons li { font-family: Menlo, Consolas, Monaco, "Courier New", monospace; }

.pb-icons i { font-size: 30px; padding: 5px; display: inline-block; }

.status.status-success { color: #3e53a4; }

.status.status-error { color: #dc0000; }

.status.status-warning { color: #ee6700; }

.fillin-blank { border-bottom: 1px solid #4e4e4e; width: 150px; display: inline-block; margin-right: 3px; }

.productplan { border: 1px solid #e1e1e1; }

figure .modal-dialog { margin-top: 0; }

.doc-gallery .image-gallery-item { margin-top: 10px; margin-bottom: 10px; transition: all .3s; }

.doc-gallery .image-gallery-item:focus { outline: none; }

.doc-gallery .image-gallery-item:hover { cursor: pointer; box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.1); transform: scale(1.05); }

.doc-gallery .image-gallery-item.active { border-color: #3e53a4; box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.1); }

.outlined, .bordered { border: 1px solid #e1e1e1; }

.header-sample { background-color: #9b9b9b; }

.toastr-samples img { cursor: pointer; margin-left: -8px; }

.table.roadmap .table-details > td { background-image: none !important; }

span.pre-release { font-family: 'Noto Sans', 'Consolas', 'Helvetica Neue', Helvetica, Arial, sans-serif; }
